import { SignarlService } from './_core/signarl.service';
import { Component, OnDestroy, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit, OnDestroy {
  title = 'angular-signarl';
  smallUnit: any = 0
  bigUnit: any = 0
  scalingKG: string = 'k';
  constructor(
    private signarlService: SignarlService,
  ) {
  }

  ngOnDestroy(): void {
    // this.subscription.forEach(item => item.unsubscribe());
    // this.offSignalr();
    this.signarlService.numberOfAttempts = 5;
    this.signarlService.close().then((result) => {
      console.log('Mixing service stopped connection');
    }).catch((err) => {
      console.log('Mixing service can not stopped connection', err);
    });
  }

  ngOnInit() {
    this.signarlService.connect();
    this.signal()
  }
  private signal() {
    this.signarlService.receiveAmount.subscribe(res => {
      let unit = '';
      let amount = 0 ;
      if(res !== null) {
        unit = res.unit
        amount = res.amount
      }
      if (unit === this.scalingKG) {
        this.bigUnit = amount
      }else {
        this.smallUnit = amount
      }
    });
  }
}
